import React from 'react';
import fetch from '../../core/fetch';
import Topic from './Topic';
export const path = '/topics/*/view';
export const action = async (state) => {
  const topicId = state.path.replace('/topics/', '').replace('/view', '');
  const query = `{
  topic(path: "${topicId}") {
    owner {
      nickname
      avatar
    }
    title
    cover
    subTitle
    viewCount
    collectCount
    content
    strategies {
      strategyId
      tryCount
      score
      cover
      title
      owner{
        nickname
        avatar
      }
    }
  }
}
`
  const response = await fetch(`/graphql?query=${query}`);
  const { data } = await response.json();
  const title = data.topic.title;
  state.context.onSetMeta('og:type', 'article');
  state.context.onSetMeta('og:image', data.topic.cover);
  state.context.onSetMeta('og:title', data.topic.title);
  state.context.onSetMeta('og:description', data.topic.subTitle);
  state.context.onSetMeta('description', data.topic.subTitle);
  state.context.onSetTitle(title);
  return <Topic topic={ data.topic } />;
};



