import React, { PropTypes } from 'react';
import withStyles from 'isomorphic-style-loader/lib/withStyles';
import s from './Photo.scss';
import Comments from '../../../src/components/Comment/Comments';
import DateFormat from '../../util/dateFormat';
import Image from '../../components/Image/Image';
function Photo({ photo }) {
  return (
    <div className={ s.container }>
      <div className= { s.cover }>
        <Image height="768" width="768" src= { photo.content[0].img } />
      </div>
      <div className={ s.author }>
        <div className= { s.header } >
          <Image height="82" width="82" src= { photo.owner.avatar } type="avatar" />
        </div>
        <div className= { s.body }>
          <p className= { s.nickname }>
            { photo.owner.nickname }:
          </p>
          <p className= { s.content }>
            { photo.content[0].desc }
          </p>
          <p className= { s.time } >
            { DateFormat.postDate(photo.createdAt) }
          </p>
        </div>
      </div>
      <Comments comments= { photo.comments } commentCount={ photo.commentCount }/>
    </div>
  );
}

Photo.propTypes = {
  photo: PropTypes.object.isRequired,
};

export default withStyles(Photo, s);



