import React, { PropTypes, Component } from 'react';
import withStyles from 'isomorphic-style-loader/lib/withStyles';
import s from './Order.scss';
import NumberFormat from '../../util/NumberFotmat';
import Location from '../../core/Location';
import fetch from '../../core/fetch';
var pingpp = require('pingpp-js');
class Order extends Component {

  constructor(props, context) {
    super(props, context);
    this.state = {
      price : props.coupon? 0: props.podcast.price/100,
      selectedCoupon: props.coupon
    }
  }

  payment() {
    var podcastId = this.props.podcast.podcastId;
    var coupon = this.props.coupon;
    var token = this.props.token;
    var self = this;
    var openId = this.props.openId;
    if(coupon) {
      fetch(`/paymentByCoupon`, {
        headers: {
          'Accept': 'application/json',
          'Content-Type': 'application/json'
        },
        method: 'POST',
        body: JSON.stringify({
          podcastId: podcastId,
          couponCode: coupon,
          token: token
        })
      })
        .then(response => response.json())
        .then(json => {
          self.props.context.window.location = '/podcasts/'+podcastId+'/view';
        });
    }else {

      fetch(`/orders`, {
        headers: {
          'Accept': 'application/vnd.yuanzi.v4+json',
          'Content-Type': 'application/json'
        },
        method: 'POST',
        body: JSON.stringify({
          podcastId: podcastId,
          token: token
        })
      }).then(response => response.json())
        .then(d => {
          console.log(d);
          fetch(`/payment`, {
            headers: {
              'Accept': 'application/vnd.yuanzi.v4+json',
              'Content-Type': 'application/json'
            },
            method: 'POST',
            body: JSON.stringify({
              orderId: d.orderId,
              openId: openId,
              token: token
            })
          })
            .then(r => r.json())
            .then(json => {
              console.log(json)
              pingpp.createPayment(json.charge, function(result, err){
                if (result == "success") {
                  // 只有微信公众账号 wx_pub 支付成功的结果会在这里返回，其他的支付结果都会跳转到 extra 中对应的 URL。
                  self.props.context.window.location = '/podcasts/'+podcastId+'/view';
                } else if (result == "fail") {
                  // charge 不正确或者微信公众账号支付失败时会在此处返回
                  return <div></div>
                } else if (result == "cancel") {
                  // 微信公众账号支付取消支付
                  return <div></div>
                }
              });
            });
        });
    }


  }

  chooseCoupons() {
    Location.push({ pathname: '/podcastdetail/'+this.props.podcastId+'/coupons', state: { podcast: this.props.podcast } });
  }

  render() {
    return (
      <div className={s.order}>
        <ul>
          <li>
            <div className={s.payment}>
              <div className={s.method}>
              <p className={s.title}>支付方式</p>
                </div>
              <hr/>
              <div className={s.wechat}>
                <img className={ s.logo } src="../../icon_wechat@2x.png" alt="" />
                <div className={ s.desc }>
                  <p className={ s.title }>微信支付</p>
                </div>
                <div className={s.space}/>
                <img className={s.logo} style={{ width:17, height: 17 }} src="../../select@2x.png" alt="" />
              </div>
            </div>
          </li>
          <li>
            <div className={s.coupon} onClick={this.chooseCoupons.bind(this)}>
              <div className={ s.title }>优惠券</div>
              <div className={s.space}/>
              <div className={ s.title }>{this.state.selectedCoupon?  '畅听一节': '请选择优惠券'}</div>
              <a href="#"><img className={s.logo} src="../../icon_next_grey@2x.png" alt="" /></a>
            </div>
          </li>
        </ul>
        <div className={s.pay}>
          <div className={ s.desc }>
            <p className={ s.title }>订单金额</p>
            <p className={ s.subtitle }>{NumberFormat.money(this.state.price)}</p>
          </div>
          <a onClick={this.payment.bind(this)}>去支付</a>
        </div>
      </div>

    );
  }
}


export default withStyles(Order, s);



