import fetch from '../../core/fetch';
import PaymentType from '../types/PaymentType';
import {
  GraphQLString as StringType,
  GraphQLNonNull as NonNull,
} from 'graphql';
require('dotenv').config();
// React.js News Feed (RSS)
const baseUrl = process.env.URL_PRODUCTION || 'http://test.iyuanzi.com';
let lastFetchTask;
const topic = {
  type: PaymentType,
  args: {
    orderId: { type: new NonNull(StringType) },
    token: { type: new NonNull(StringType) },
    openId: { type: new NonNull(StringType) },
  },
  resolve({ request }, { orderId, token, openId }) {
    token = token.replace(/ /g,'+');
    return fetch(`${baseUrl}/payment`, {
      headers: {
        'Accept': 'application/vnd.yuanzi.v4+json',
        'Authorization': 'Bearer '+token,
        'Content-Type': 'application/json'
      },
      method: 'POST',
      body: JSON.stringify({
        orderId: orderId,
        channel: 'wx_pub',
        openId: openId
      })
    })
      .then(response => response.json())
      .then(data => {
        return data;
      });
  },
};

export default topic;




