import {
  GraphQLObjectType as ObjectType,
  GraphQLString as StringType,
} from 'graphql';

const StepsType = new ObjectType({
  name: 'Step',
  fields: {
    _id: { type: StringType },
    description: { type: StringType },
    imgUrl: { type: StringType },
    stepId: { type: StringType },
    timePoint: { type: StringType },
  },
});


export default StepsType;


