import {
  GraphQLObjectType as ObjectType,
  GraphQLString as StringType,
  GraphQLList as List,
  GraphQLInt as IntType,
} from 'graphql';

import CommentType from './CommentType';
import UserType from './UserType';
import ParticipantType from './ParticipantType';
import StrategyType from './StrategyType';

const EventType = new ObjectType({
  name: 'Event',
  fields: {
    owner: { type: UserType },
    title: { type: StringType },
    content: { type: StringType },
    number: { type: StringType },
    price: { type: StringType },
    participantCount: { type: IntType },
    cover: { type: StringType },
    comments: { type: new List(CommentType) },
    startDate: { type: StringType },
    endDate: { type: StringType },
    location: { type: StringType },
    enrollCount: { type: IntType },
    participants: { type: new List(ParticipantType) },
    commentCount: { type: IntType },
    minAge: { type: IntType },
    maxAge: { type: IntType },
    minNumber: { type: IntType },
    strategy: { type: new ObjectType({
      name: 'StrategySimple',
      fields: {
        owner: { type: UserType },
        title: { type: StringType },
        strategyId: { type: StringType },
      },
    })},
  },
});

export default EventType;



