import React, { PropTypes, Component } from 'react';
import withStyles from 'isomorphic-style-loader/lib/withStyles';
import s from './Podcast.scss';
import Image from '../../components/Image/Image';
import DateFormat from '../../util/dateFormat';
import NumberFormat from '../../util/NumberFotmat';
import Linkify from 'react-linkify';
import moment from 'moment';
import Location from '../../core/Location';
import fetch from '../../core/fetch';
class Podcast extends Component {

  constructor(props, context) {
    super(props, context);
    this.tick = this.tick.bind(this);
    this.state = { date: moment(this.props.podcast.startDate).fromNow() }
  }


  tick() {
    this.setState({
      date: moment(this.props.podcast.startDate).fromNow()
    })
  }
  componentDidMount() {
    // setInterval(this.tick, 1000);
  }

  componentWillUnmount() {
    // clearInterval(this.tick)
  }

  renderScore(podcast) {
    return
    <span style={{ color: '#ff989e' }}>{ podcast.enrollCount }</span>&nbsp;人学过
    评分&nbsp;<span style={{ color: '#ff989e' }}>{ podcast.userScore>0?podcast.userScore: 9.9 }</span>&nbsp;分

  }

  redirectOrder(event) {

    let podcast = this.props.podcast;
    let token = this.props.token || 'unsign';
    if(podcast.joined) {
      Location.push('/podcastdetail/'+podcast.podcastId+'/view');
    }
    else if(!podcast.joined && podcast.price == 0){

      const q = `{
  order(podcastId: "${podcast.podcastId}", token: "${token}", ) {
    orderId
  }
}`;
      if(token != 'unsign') {
        fetch(`/graphql?query=${q}`);
      }
      Location.push('/podcastdetail/'+podcast.podcastId+'/view');
    }else {
      this.props.context.window.location = '/podcastdetail/order/'+podcast.podcastId;
      this.props.context.window.reload()
    }

  }

  render() {
    let feature = moment(this.props.podcast.startDate) >  moment();
    let podcast = this.props.podcast;
    return (
      <div className={s.container}>
        <div style={{ margin:'0 auto',display:'none' }}>
          <img src={ this.props.podcast.cover }  alt=""/>
        </div>
        <div className={ s.cover }>

          <Image width="768" height="200" src={ this.props.podcast.cover } />
          <div className={ s.startLesson}>
            {feature? (
              <div className={s.countdown}>
                <p className={s.countdownLabel}>倒计时</p>
                <p className={s.date}>{this.state.date}</p>

              </div>
            ):   <a onClick={this.redirectOrder.bind(this)} style={{ height: '225px' }}>
              <img src="../../btnStart@2x.png"  style={{ width: '50%' }}/>
            </a>}
          </div>
          <div className={ s.shadow } ></div>
        </div>
        <h3 className={ s.title }>{ podcast.title }</h3>
        <p className= { s.lecturer }><span>{ podcast.lecturer }</span>{'直播时间: ' +DateFormat.commentDate(podcast.startDate) }<span></span></p>
        <hr className={ s.borderHr }/>
        <div className={ s.count }>
          <span className= { s.value }>{ podcast.price == 0? '免费': NumberFormat.money(podcast.price / 100) }</span>
          {feature?"": this.renderScore(podcast)  }
        </div>
        <div className={ s.podcastsCount }>
          <span>课程概述</span>
        </div>
        <Text content={podcast.content.replace(/\n/g, "<br />")}/>
        <div className={ s.podcastsCount }>
          <span>讲师简介</span>
        </div>
        <div className={s.lecturerAvatarCoverGroup}>
          <img className={s.lecturerAvatarCover} width="80" height="80" src={ podcast.lecturerAvatar+"@20-15bl.jpg" }/>
          <img className={s.lecturerAvatar} width="80" height="80" src={ podcast.lecturerAvatar }/>
        </div>
        <Text content={podcast.lecturerIntroduction.replace(/\n/g, "<br />")}/>
        <hr style={{ borderTop: '5px solid #ededed', width: '100%' }}/>
        <a href="/podcasts" style={{ width: '100%' }}>
          <div style={{ height: '50px', width: '100%', textAlign:'center', marginTop: '20px', fontSize:'15px' }}>更多精彩课程</div>
        </a>
      </div>
    );
  }
}

var Text = React.createClass({
  render: function() {
    return <p style={{ overflow: 'hidden' }}><Linkify>{ breakLine(this.props.content) }</Linkify></p>;
  }
});




function breakLine(text) {
  var regex = /(<br \/>)/g;
  return text.split(regex).map(function(line) {
    return line.match(regex) ? React.createElement('br') : line;
  });
}


Podcast.propTypes = {
  podcast: PropTypes.object.isRequired,
};

export default withStyles(Podcast, s);



