import React, { PropTypes } from 'react';
import withStyles from 'isomorphic-style-loader/lib/withStyles';
import s from './Topics.scss';
import Image from '../../components/Image/Image';
import DateFormat from '../../util/dateFormat';
import NumberFormat from '../../util/NumberFotmat';
import cx from 'classnames';
function Podcasts({ topics }) {
  let p = topics.topics;
  return (
    <div className={s.topics}>
      <ul>
        {
          p.map((item, key) => {
            let roomId = '/topics/'+item.topicId+'/view'
            let background = 'url(' + item.cover + ')';
            return (
              <a href={ roomId } key= { item.topicId }>
                <li>
                  <div className={s.topic} style={ { backgroundImage: background}}>
                    <div>
                      <div className={s.overlay}></div>
                      <p className={s.title}>{item.title}</p>
                      <p className={s.count}>
                        <img src="../icon_eye@2x.png" alt=""/>
                        {item.viewCount}
                        <img src="../icon_collect@2x.png" alt=""/>
                        {item.collectCount}
                        </p>
                    </div>
                  </div>


                </li>
              </a>
            );
          })
        }
      </ul>
    </div>

  );
}
Podcasts.propTypes = {
};

export default withStyles(Podcasts, s);



