//
// Register server-side rendering middleware
// -----------------------------------------------------------------------------
server.get('/podcastdetail*', function(req, res, next) {
  try {
    var decoded = jwt.verify(req.cookies.id_token, auth.jwt.secret);
    passport.authenticate('wechatNo', {
      session: true,
      callbackURL: 'http://' + req.headers.host + req.path
    })(req, res, next);
  } catch(err) {
    passport.authenticate('wechat', {
      session: true,
      callbackURL: 'http://' + req.headers.host + req.path
    })(req, res, next);
  }
}, async (req, res, next) => {

  try {
    let statusCode = 200;
    const template = require('./views/index.jade');
    const data = { title: '', description: '', css: '', body: '', entry: assets.main.js };

    if (process.env.NODE_ENV === 'production') {
      data.trackingId = analytics.google.trackingId;
    }


    const css = [];
    const context = {
      insertCss: styles => css.push(styles._getCss()),
      onSetTitle: value => (data.title = value),
      onSetMeta: (key, value) => (data[key] = value),
      onPageNotFound: () => (statusCode = 404),
    };

    await Router.dispatch({ path: req.path, query: req.query, context }, (state, component) => {
      data.body = ReactDOM.renderToString(component);
      data.css = css.join('');
      data.appData = JSON.stringify(req.user)

    });

    res.status(statusCode);
    const expiresIn = 60 * 60 * 24 * 180; // 180 days
    const token = jwt.sign(req.user, auth.jwt.secret, { expiresIn: expiresIn});
    res.cookie('id_token', token, { maxAge: 1000 * expiresIn, httpOnly: true });
    res.send(template(data));
  } catch (err) {
    next(err);
  }
});

server.get('*', async (req, res, next) => {
  try {
    let statusCode = 200;
    const template = require('./views/index.jade');
    const data = { title: '', description: '', css: '', body: '', entry: assets.main.js };

    if (process.env.NODE_ENV === 'production') {
      data.trackingId = analytics.google.trackingId;
    }

    const css = [];
    const context = {
      insertCss: styles => css.push(styles._getCss()),
      onSetTitle: value => (data.title = value),
      onSetMeta: (key, value) => (data[key] = value),
      onPageNotFound: () => (statusCode = 404),
    };

    await Router.dispatch({ path: req.path, query: req.query, context }, (state, component) => {
      data.body = ReactDOM.renderToString(component);
      data.css = css.join('');
      try {
        var decoded = jwt.verify(req.cookies.id_token, auth.jwt.secret);
        data.appData = JSON.stringify(decoded)
      } catch(err) {
      }
    });

    res.status(statusCode);

    res.send(template(data));
  } catch (err) {
    next(err);
  }
});

//
// Error handling
// -----------------------------------------------------------------------------
const pe = new PrettyError();
pe.skipNodeFiles();
pe.skipPackage('express');

server.use((err, req, res, next) => { // eslint-disable-line no-unused-vars
  console.log(pe.render(err)); // eslint-disable-line no-console
  const template = require('./views/error.jade');
  const statusCode = err.status || 500;
  res.status(statusCode);
  res.send(template({
    message: err.message,
    stack: process.env.NODE_ENV === 'production' ? '' : err.stack,
  }));
});

//
// Launch the server
// -----------------------------------------------------------------------------
server.listen(port, () => {
  /* eslint-disable no-console */
  console.log(`The server is running at http://localhost:${port}/`);
});



