import {
  GraphQLObjectType as ObjectType,
  GraphQLString as StringType,
} from 'graphql';
import UserType from './UserType';

const ParticipantType = new ObjectType({
  name: 'Participant',
  fields: {
    participant: { type: UserType },
  },
});

export default ParticipantType;



