import React from 'react';
import Coupons from './Coupons';
export const path = '/podcastdetail/*/coupons';
import fetch from '../../core/fetch';
export const action = async (state) => {
  if(state.context.appData && state.context.appData.length>0) {
    let d = state.context.appData.replace(/&quot;/g,'"');
    let appData = JSON.parse(d);
    let token = appData.access_token;
    // let token = 'KF57CbAJPLm11476pXX66SRgRHdLUceGXcLVUzy0CLo=';
    const query = `{
  coupons(token: "${token}") {
     coupons {
      expired
      coupon
    }
  }
}
`;

    const podcastId = state.path.replace('/podcastdetail/', '').replace('/coupons', '');
    const response = await fetch(`/graphql?query=${query}`);
    const { data } = await response.json();


    state.context.onSetMeta('og:title', '选择优惠券');
    state.context.onSetMeta('title', '选择优惠券');
    return <Coupons coupons={data.coupons.coupons} podcastId={podcastId} podcast={state.podcast}/>;
  }

  return <div></div>

};



