import React from 'react';
import fetch from '../../core/fetch';
import Event from './Event';
export const path = '/events/*/view';
export const action = async (state) => {
  const eventId = state.path.replace('/events/', '').replace('/view', '');
  const query = `{
  event(path: "${eventId}") {
    strategy {
      title
      strategyId
    }
    title
    cover
    startDate
    endDate
    content
    maxAge
    minAge
    minNumber
    number
    enrollCount
    location
    enrollCount
    number
    price
    commentCount
    comments {
      commentUser {
        nickname
        avatar
      }
      createdAt
      content
      commentId
      images
    }
    participants {
      participant {
        avatar
        nickname
        userId
      }
    }
    owner {
      avatar
      nickname
    }
  }
}
`
  /*
  * number 需要报名人数
  * enrollCount 已报名人数
  * */
  const response = await fetch(`/graphql?query=${query}`);
  const { data } = await response.json();
  const title = data.event.title;
  state.context.onSetMeta('og:type', 'article');
  state.context.onSetMeta('og:image', data.event.cover);
  state.context.onSetMeta('og:title', data.event.title);
  state.context.onSetMeta('og:description', data.event.subTitle);
  state.context.onSetMeta('description', data.event.subTitle);
  state.context.onSetTitle(title);
  return <Event event= { data.event } />;
};



