var TimeFormat = {
  audioTime: (value) => {
    const times = new Array(3600, 60, 1);
    let time = '';
    let tmp;
    for (let i = 0; i < times.length; i++) {
      tmp = Math.floor(value / times[i]);
      if (tmp < 1) {
        tmp = '00';
      } else if (tmp < 10) {
        tmp = `0${tmp}`;
      }
      time += tmp;
      if (i < 2) {
        time += ':';
      }
      value = value % times[i];
    }
    return time;
  },
};

export default TimeFormat;




