import React,{ PropTypes } from 'react';
import UrlFormat from '../../util/UrlFormat';
class Image extends React.Component {
  constructor(props) {
    super(props);
    const style = {
      width: props.width || '',
      height: props.height || '',
      pr: props.pr || 1,
    };
    const urlFormat = new UrlFormat(props.src);
    this.state = {
      imgUrl: urlFormat.cdn().progressive(style.pr).height(style.height).width(style.width).url(),
    };
    this.type = props.type || 'cover';
  }
  render() {
    return (
      <img className= { this.props.className } src= { this.state.imgUrl } onError={this.handleImageErrored.bind(this)} />
    );
  }
  handleImageErrored() {
    const defaultImg = {
      avatar: '../../default-avatar.png',
      cover: '../../default-cover.png',
    }
    this.setState(
      { imgUrl: defaultImg[this.type] }
    );
  }
}
export default Image;



