import {
  GraphQLObjectType as ObjectType,
  GraphQLString as StringType,
  GraphQLList as List,
  GraphQLInt as IntType,
  GraphQLBoolean as BooleanType
} from 'graphql';

import CommentType from './CommentType';
import UserType from './UserType';

const PodcastType = new ObjectType({
  name: 'Podcast',
  fields: {
    podcastId: { type: StringType },
    title: { type: StringType },
    cover: { type: StringType },
    price: { type: StringType },
    lecturer: { type: StringType },
    lecturerIntroduction: { type: StringType },
    lecturerAvatar: { type: StringType },
    content: { type: StringType },
    startDate: { type: StringType },
    enrollCount: { type: IntType },
    userScore: { type: StringType },
    roomNumber: { type: StringType },
    joined: { type: BooleanType },
    startDate: { type: StringType }
  },
});

export default PodcastType;
