import {
  GraphQLSchema as Schema,
  GraphQLObjectType as ObjectType,
} from 'graphql';

import me from './queries/me';
import content from './queries/content';
import news from './queries/news';
import strategy from './queries/strategy';
import topic from './queries/topic';
import event from './queries/event';
import photo from './queries/photo';
import article from './queries/article';
import podcast from './queries/podcast';
import podcasts from './queries/podcasts';
import order from './queries/order';
import payment from './queries/payment';
import topics from './queries/topics';
import coupons from './queries/coupons';
const schema = new Schema({
  query: new ObjectType({
    name: 'Query',
    fields: {
      me,
      content,
      news,
      strategy,
      topic,
      photo,
      event,
      article,
      podcast,
      podcasts,
      order,
      payment,
      topics,
      coupons
    },
  }),
});

export default schema;



