import React, { PropTypes, Component } from 'react';
import withStyles from 'isomorphic-style-loader/lib/withStyles';
import s from './Podcasts.scss';
import Image from '../../components/Image/Image';
import DateFormat from '../../util/dateFormat';
import NumberFormat from '../../util/NumberFotmat';
import moment from 'moment';
import Location from '../../core/Location';
class Podcasts extends Component {

  constructor(props, context) {
    super(props, context);
  }

  render() {
    let podcasts = this.props.podcasts;
    let p = podcasts.podcasts;
    return (
      <div className={s.events}>
        <ul>
          {
            p.map((item, key) => {


              let roomId = '/podcasts/'+item.podcastId+'/view';
              let feature = moment(item.startDate)> moment();
              return (
                <a href={ roomId } key= { item.podcastId }>
                  <li>
                    <Image height="132" width="228" src= { item.cover } />
                    <div className= { s.body }>
                      <p className= { s.title }>{ item.title }</p>
                      {feature?"":<p className= { s.time }>{ DateFormat.commentDate(item.startDate) }</p>}
                      <p className= { s.price }>{ item.price === '0' ? '免费' : `${NumberFormat.money(item.price/100)} 元` } </p>
                      <div style={{ display: 'flex' }}>
                        <div className={s.lecturer}>{item.lecturer}</div>
                        <div className= { s.participate }>{ item.enrollCount }<span>人学过</span></div>

                      </div>
                    </div>
                  </li>
                </a>
              );
            })
          }
        </ul>
      </div>

    );
  }
}


Podcasts.propTypes = {
};

export default withStyles(Podcasts, s);



