import {
  GraphQLObjectType as ObjectType,
  GraphQLString as StringType,
  GraphQLList as List,
  GraphQLInt as IntType,
} from 'graphql';

import PhotoType from './PhotoType';
import CommentType from './CommentType';
import UserType from './UserType';
import MaterialType from './MaterialType';
import ToolsType from './ToolsType';
import StepsType from './StepsType';
import EventType from './EventType';
const StrategyType = new ObjectType({
  name: 'Strategy',
  fields: {
    owner: { type: UserType },
    title: { type: StringType },
    subTitle: { type: StringType },
    collectCount: { type: StringType },
    soundStory: { type: StringType },
    tryCount: { type: StringType },
    score: { type: StringType },
    description: { type: StringType },
    cover: { type: StringType },
    photoCount: { type: IntType },
    photos: { type: new List(PhotoType) },
    comments: { type: new List(CommentType) },
    commentCount: { type: IntType },
    materials: { type: new List(MaterialType) },
    tools: { type: new List(ToolsType) },
    steps: { type: new List(StepsType) },
    scope: { type: IntType },
    consumingTime: { type: IntType },
    degree: { type: IntType },
    strategyId: { type: StringType },
    events: { type: new List(EventType) },
  },
});

export default StrategyType;



