import React from 'react';
import fetch from '../../core/fetch';
import Podcast from './Podcast';
export const path = '/podcasts/*/view';
export const action = async (state) => {
  if(state.context.appData && state.context.appData.length>0) {
    let d = state.context.appData.replace(/&quot;/g,'"');
    let appData = JSON.parse(d);
    let token = appData.access_token || "unsign";
    const podcastId = state.path.replace('/podcasts/', '').replace('/view', '');
    const query = `{
  podcast(path: "${podcastId}", token: "${token}") {
    podcastId
    title
    cover
    price
    lecturer
    lecturerIntroduction
    lecturerAvatar
    content
    startDate
    enrollCount
    userScore
    joined
    roomNumber
  }
}
`
    const response = await fetch(`/graphql?query=${query}`);
    const { data } = await response.json();
    const title = data.podcast.title;
    state.context.onSetMeta('og:type', 'podcast');
    state.context.onSetMeta('og:image', data.podcast.cover);
    state.context.onSetMeta('image', data.podcast.cover);
    state.context.onSetMeta('og:title', data.podcast.title);
    state.context.onSetMeta('description', data.podcast.content);
    state.context.onSetTitle(title);
    return <Podcast podcast= { data.podcast } context={state} token={token}/>;
  }else {
    const podcastId = state.path.replace('/podcasts/', '').replace('/view', '');
    const query = `{
  podcast(path: "${podcastId}", token: "unsign") {
    podcastId
    title
    cover
    price
    lecturer
    lecturerIntroduction
    lecturerAvatar
    content
    startDate
    enrollCount
    userScore
    joined
    roomNumber
  }
}
`
    const response = await fetch(`/graphql?query=${query}`);
    const { data } = await response.json();
    const title = data.podcast.title;
    state.context.onSetMeta('og:type', 'podcast');
    state.context.onSetMeta('og:image', data.podcast.cover);
    state.context.onSetMeta('image', data.podcast.cover);
    state.context.onSetMeta('og:title', data.podcast.title);
    state.context.onSetMeta('description', data.podcast.content);
    state.context.onSetTitle(title);
    return <Podcast podcast= { data.podcast } context={state} token={'unsign'}/>;
  }

};



