import React from 'react';
import fetch from '../../core/fetch';
import Article from './Artcle';
export const path = '/articles/*/view';
export const action = async (state) => {
  const articleId = state.path.replace('/articles/', '').replace('/view', '');
  const query = `{
  article(path: "${articleId}") {
    owner {
      nickname
      avatar
    }
    title
    photos
    createdAt  
    comments {
      commentUser {
        nickname
        avatar
      }
      createdAt
      content
      commentId
      images
    }
    commentsCount
    
  }
}
`
  const response = await fetch(`/graphql?query=${query}`);
  const { data } = await response.json();
  const title = data.article.title;
  state.context.onSetMeta('og:type', 'article');
  state.context.onSetMeta('og:image', data.article.photos[0]);
  state.context.onSetMeta('og:title', data.article.title);
  state.context.onSetMeta('format-detection', 'telephone-no');
  state.context.onSetTitle(title);
  return <Article article={ data.article } />;
};



