import React, { PropTypes } from 'react';
import withStyles from 'isomorphic-style-loader/lib/withStyles';
import s from './Event.scss';
import Comments from '../../components/Comment/Comments';
import DateFormat from '../../util/dateFormat';
import NumberFormat from '../../util/NumberFotmat';
import Footer from '../../components/Footer/Footer';
import Image from '../../components/Image/Image';

function Event({ event }) {
  return (
    <div className= { s.container }>
      <div className= { s.cover }>
        <Image className= { s.cover } height="450" src= { event.cover } />
        <div className= { s.shadow }></div>
      </div>
      <div className= { s.user }>
        <Image className= { s.avatar } height="100" width="100" src= { event.owner.avatar } type="avatar" />
        <div>
          <p className= { s.title }>{ event.title }</p>
          <p className= { s.nickname }>by  { event.owner.nickname }</p>
        </div>
      </div>
      <div className= { s.info }>
        <div className={ s.startDate }>
          <img src="../../icon_time.png" alt="" />
          <span className= { s.title }>开始时间</span>
          <span className= { s.value }>{ DateFormat.postDate(event.startDate) }</span>
        </div>
        <div className={ s.endDate }>
          <img src="../../icon_time.png" alt="" />
          <span className= { s.title }>结束时间</span>
          <span className= { s.value }>{ DateFormat.postDate(event.endDate) }</span>
        </div>
        <div className={ s.age }>
          <img src="../../icon_age.png" alt="" />
          <span className= { s.title }>适合年纪</span>
          <span className= { s.value }>{ event.minAge } - { event.maxAge }岁</span>
        </div>
        <div className={ s.location } >
          <img src="../../icon_mark.png" alt="" />
          <span className= { s.title }>活动地点</span>
          <span className= { s.value }>{ event.location }</span>
        </div>
        <div className={ s.number }>
          <img src="../../icon_count.png" alt="" />
          <span className= { s.title }>人数限制</span>
          <span className= { s.value }>{ event.number } 人</span>
        </div>
        <div className={ s.price }>
          <img src="../../icon_pluse.png" alt="" />
          <span className= { s.title }>活动价格</span>
          <span className= { s.value }>{ NumberFormat.money(event.price / 100) } 元/人</span>
        </div>
      </div>
      <div className= { s.content } dangerouslySetInnerHTML={{ __html: event.content }}></div>
      { renderStrategy(event.strategy) }
      { renderParticipants(event.participants) }
      <div>
        <Comments comments={ event.comments } commentCount={ event.commentCount }/>
      </div>
      <Footer />
    </div>

  );
}
function renderParticipants(participants) {
  if (!participants.length || participants.length == 0) {
    return null;
  }
  return (
    <div className= { s.participants }>
      <p>参与活动<span>({ participants.length })</span></p>
      <ul>
        {
          participants.map((item, key) => {
            if (key > 3){
              return null;
            }
            return (
              <li key={ key }>
                <Image height="64" width="64" src={ item.participant.avatar }/>
              </li>
            )
          })
        }
      </ul>
    </div>
  );
}

function renderStrategy(strategy) {
  if (strategy == null) {
    return null;
  }
  return (
    <div className= { s.strategy }>
      <div className= { s.triangle }></div>
      <div className= { s.content }>
        <p>来自妙招：</p>
        <div>
          <a href= { `/strategies/${ strategy.strategyId }/view` }>{ strategy.title }</a>
          <img src="../../icon_next_grey.png" />
        </div>
      </div>
    </div>
  );
}

Event.propTypes = {
  event: PropTypes.object.isRequired,
};
export default withStyles(Event, s);



