import React, { PropTypes } from 'react';
import withStyles from 'isomorphic-style-loader/lib/withStyles';
import s from './Artcle.scss';
import Comments from '../../components/Comment/Comments';
import Image from '../../components/Image/Image';
import DateFormat from '../../util/dateFormat';
function Article({ article }) {
  return (
    <div className={s.container}>
      <div className={s.header}>
        <Image className= { s.avatar } height="64" width="64" src= { article.owner.avatar } type="avatar" />
        <div>
          <p className={ s.nickname }>{ article.owner.nickname }</p>
          <p className={ s.time }>{ DateFormat.commentDate(article.createdAt) }</p>
        </div>
      </div>
      <hr/>
      <div className={ s.main }>
        <p className={s.content}>{ article.title }</p>
        <ul className= { s.images }>
          {
            article.photos.map(value => {
              return (
                <li key={ value }>
                  <Image height="230" width="230" src= { value } />
                </li>
              );
            })
          }
        </ul>
      </div>
      <Comments comments={ article.comments } commentCount={ article.commentsCount }/>
    </div>
  );
}
Article.propTypes = {
  article: PropTypes.object.isRequired,
};

export default withStyles(Article, s);



