import React, { PropTypes } from 'react';
import withStyles from 'isomorphic-style-loader/lib/withStyles';
import s from './Topic.scss';
import Image from '../../components/Image/Image';
function Topic({ topic }) {
  return (
    <div className= { s.container }>
      <div className= { s.cover }>
        <Image height="694" src= { topic.cover } />
        <div className={ s.shadow }></div>
      </div>
      <div className= { s.user }>
        <div className= { s.avatar }>
          <Image height="96" width="96" src= { topic.owner.avatar }/>
        </div>
        <div>
          <p className= { s.title }>{ topic.title }</p>
          <p className= { s.nickname }>by  { topic.owner.nickname }</p>
        </div>
      </div>
      <div className= { s.content }>
        <p > { topic.subTitle }</p>
      </div>
      <div className= { s.count }>
        <p>{ topic.viewCount }&nbsp;人参加</p>
        <p>{ topic.collectCount }&nbsp;人收藏</p>
      </div>
      <div className= { s.strategies }>
        <div className={ s.strategiesCount }>
          <span>妙招</span>
          <span>&nbsp;({ topic.strategies.length })</span>
        </div>
        <ul>
          {
            topic.strategies.map(item => {
              return (
                <li key= {item.strategyId}>
                  <a href={ `/strategies/${item.strategyId}/view` }>
                    <Image src= { item.cover } h="357" w="357" />
                  </a>
                  <div className= { s.main }>
                    <p className= { s.title }>{ item.title }</p>
                    <p className= { s.by }>
                      by&nbsp;
                      <span className= { s.nickname }>{ item.owner.nickname }</span>
                    </p>
                    <div className= { s.participate }><p>{ item.tryCount }人参与</p></div>
                  </div>
                </li>
              );
            })
          }
        </ul>
      </div>
    </div>
  );
}

Topic.propTypes = {
  topic: PropTypes.object.isRequired,
};

export default withStyles(Topic, s);



