import React from 'react';
import fetch from '../../core/fetch';
import Podcasts from './Podcasts';
export const path = '/podcasts/';
export const action = async (state) => {
  const query = `{
  podcasts {
     podcasts {
      podcastId
      title
      cover
      price
      lecturer
      lecturerIntroduction
      lecturerAvatar
      content
      startDate
      enrollCount
      userScore
      roomNumber
      startDate
    }
  }
}
`
  const response = await fetch(`/graphql?query=${query}`);
  const { data } = await response.json();
  state.context.onSetMeta('og:title', '精彩课程');
  state.context.onSetMeta('title', '精彩课程');
  return <Podcasts podcasts= { data.podcasts } />;
};



