import React from 'react';
import Order from './Order';
export const path = '/podcastdetail/order/*';
export const action = async (state) => {
  if(state.context.appData && state.context.appData.length>0) {
    let d = state.context.appData.replace(/&quot;/g,'"');
    let appData = JSON.parse(d);
    let token = appData.access_token;
    let openId = appData.openId;
    const podcastId = state.path.replace('/podcastdetail/order/', '');
    state.context.onSetMeta('og:title', '确认订单');
    state.context.onSetMeta('title', '确认订单');

    const query = `{
  podcast(path: "${podcastId}", token: "${token}") {
    podcastId
    title
    cover
    price
    lecturer
    lecturerIntroduction
    lecturerAvatar
    content
    startDate
    enrollCount
    userScore
    joined
    roomNumber
  }
}
`
    const response = await fetch(`/graphql?query=${query}`);
    const { data } = await response.json();
    return <Order podcastId={podcastId}
                  coupon={state.coupon} podcast={data.podcast}
                  openId={openId}
                  token={token} context={state}/>;
  }

  return <div></div>
};



