import React,{ PropTypes } from 'react';
import withStyles from 'isomorphic-style-loader/lib/withStyles';
import s from './Audio.scss';
import TimeFormat from '../../util/TimeFormat';
class Audio extends React.Component {
  constructor(props) {
    super(props);
    this.state = { isPlay: false };
  }
  onClick() {
    if (this.refs.audio.paused) {
      this.setState({ isPlay: true });
      this.refs.audio.play();
    } else {
      this.refs.audio.pause();
      this.setState({ isPlay: false });
    }
  }

  onUpdateTime() {
    const audio = this.refs.audio;
    const fill = this.refs.fill;
    const now = this.refs.now;
    const end = this.refs.end;
    const current = 100 * (audio.currentTime / audio.duration);
    fill.style.width = `${current}%`;
    now.innerHTML = TimeFormat.audioTime(audio.currentTime);
    end.innerHTML = TimeFormat.audioTime(audio.duration);
  }
  onEnded() {
    this.setState({ isPlay: false });
  }

  render() {
    return (
      <div className= { s.audio }>
        <audio ref="audio" src= { this.props.src } onTimeUpdate={ () => this.onUpdateTime() } onEnded= { () => this.onEnded() }>
        </audio>
        <i className= { s.playCircle }>
          <i className={ s.playBtn } onClick={ () => this.onClick() } >
            <img src= { this.state.isPlay ? '../../icon_pause.png' : '../../icon_play.png' } />
          </i>
        </i>
        <span ref="now" className={ s.now }>00:00:00</span>
        <span className= { s.progress }>
          <span ref="fill" className= { s.fill }>
          </span>
        </span>
        <span ref="end" className={ s.end }>00:00:00</span>
      </div>
    );
  }

}
export default withStyles(Audio, s);



