import React, { PropTypes, Component } from 'react';
import withStyles from 'isomorphic-style-loader/lib/withStyles';
import s from './Coupon.scss';
import classnames from 'classnames/bind';
let cx = classnames.bind(s);
import fetch from '../../core/fetch';
import Location from '../../core/Location';
class Coupons extends Component {

  constructor(props, context) {
    super(props, context);
    this.state = {
      value:''
    }
  }


  getCoupon() {
    if(this.state.value.length == 10) {
      var coupon = this.state.value;
      let token = this.props.token;
      fetch(`/coupons`, {
        headers: {
          'Accept': 'application/json',
          'Content-Type': 'application/json'
        },
        method: 'POST',
        body: JSON.stringify({
          couponCode:coupon,
          token: token
        })
      })
        .then(response => response.json())
        .then(json => {
          Location.goBack();
        });
    }

  }


  handleChange(event) {
    this.setState({value: event.target.value});
  }

  render() {
    var style = cx({
      'addMore': true,
      'selected': this.state.value.length == 10
    });
    return (
      <div className={s.coupons}>
        <input value={this.state.value} onChange={this.handleChange.bind(this)} type="text" name="" id="" style={{ width: '100%', height: '44', textAlign: 'center' }} placeholder="请输入优惠券" ref="couponInput"/>
        <div className={style} onClick={this.getCoupon.bind(this)}>
            <p className={s.title}>添加优惠券</p>
        </div>
      </div>

    );
  }
}


export default withStyles(Coupons, s);



