import React, { PropTypes, Component } from 'react';
import withStyles from 'isomorphic-style-loader/lib/withStyles';
import s from './Coupons.scss';
import NumberFormat from '../../util/NumberFotmat';
import classnames from 'classnames/bind';
let cx = classnames.bind(s);
import Location from '../../core/Location';
import moment from 'moment';
class Coupons extends Component {

  constructor(props, context) {
    super(props, context);
    this.state = {
      price : 0,
      coupon: {}
    }
  }

  selectedCoupon(coupon) {
    this.setState({
      coupon: coupon
    });
    Location.push({ pathname: '/podcastdetail/order/'+this.props.podcastId, state: { coupon: coupon.coupon, podcast: this.props.podcast } });
  }
  addMore() {
    Location.push({
      pathname: '/podcastdetail/'+this.props.podcastId+'/coupons/add'
    })
  }

  render() {
    var self = this;
    return (
      <div className={s.coupons}>
        <ul>
          {this.props.coupons.map(function(coupon) {

            var btnClass = cx({
              'coupon':true,
              'couponPressed': coupon == self.state.coupon,
            });
            var localTime = moment(coupon.expired).format('YYYY-MM-DD');
            return (
              <li key={coupon.coupon} onClick={self.selectedCoupon.bind(self, coupon)}>
                <div className={btnClass}>
                  <p className={s.label}>元子优惠券</p>
                  <p className={s.title}>畅听一节课</p>
                  <p className={s.expired}>有效期至: 2016-10-30</p>
                </div>
              </li>
            )
          })}
        </ul>
        <div className={s.addMore} onClick={this.addMore.bind(this)}>
            <img className={s.logo} src="../../icon_plus@2x.png" alt="" />
            <p className={ s.title }>添加优惠券</p>
        </div>
      </div>

    );
  }
}


export default withStyles(Coupons, s);



