import {
  GraphQLObjectType as ObjectType,
  GraphQLString as StringType,
  GraphQLInt as IntType,
  GraphQLBoolean as BooleanType,
  GraphQLList as List,
} from 'graphql';


const p = new ObjectType({
  name: 'Payment',
  fields: {
    charge: { type: new ObjectType({
      name: 'Charge',
      fields: {
        id: { type: StringType},
      }
    })},
  },
});

export default p;



