import {
  GraphQLObjectType as ObjectType,
  GraphQLString as StringType,
} from 'graphql';

const ToolsType = new ObjectType({
  name: 'Tool',
  fields: {
    amount: { type: StringType },
    title: { type: StringType },
  },
});
export default ToolsType;


