import moment from 'moment';
moment.locale('zh-cn');
var DateFormat = {
  postDate: (date) => {
    return moment(date).format('YYYY 年 MM 月 DD 日 HH:mm');
  },
  commentDate: (date) => {
    return moment(date).format('YYYY-MM-DD HH:mm ');
  },
};
export default DateFormat;



