import React,{ PropTypes } from 'react';
import withStyles from 'isomorphic-style-loader/lib/withStyles';
import s from './ChatVoice.scss';
class Audio extends React.Component {
  constructor(props) {
    super(props);
    this.state = { isPlay: false, duration: '0', };
  }

  componentDidMount() {
    let audio = this.refs.audio;
    let self = this;
    audio.addEventListener('loadedmetadata',function(){
      self.setState({ duration: audio.duration });
    },false)
  }

  componentWillReceiveProps(nextProps) {
    if(nextProps.isPlay) {
      let self = this;
      var images = ["../../voice1@2x.png", "../../voice2@2x.png", "../../voice3@2x.png"];
      var i = 0;
      setInterval(function () {
        i++;
        if (i == images.length) {
          i = 0;
        }
        self.setState({
          images: images[i]
        })
      }, 500);

    }
    this.setState({ isPlay: nextProps.isPlay })
  }

  render() {
    let width = this.state.duration/2>5 ? this.state.duration/2: 5;
    return (
      <div style={{ flexDirection: 'row', display: 'flex' }} onClick={this.props.changeAudio}>
        <div className={s.content}>
          <div style={{ width: width+'vw', minHeight: '24px' }}>
            <img src={this.state.isPlay? this.state.images : '../../voice3@2x.png'} alt="" style={{ height: '24px' }}/>
            <audio ref="audio" src={this.props.audioUrl}/>
          </div>
        </div>
        {this.state.duration>0?<var className={s.duration}>{parseInt(this.state.duration)+'″'}</var>:''}
      </div>
    );
  }

}
export default withStyles(Audio, s);



