import React from 'react';
import fetch from '../../core/fetch';
import Photo from './Photo';
export const path = '/photos/*/view';
export const action = async (state) => {
  const photoId = state.path.replace('/photos/', '').replace('/view', '');
  const query = `{
  photo(path: "${photoId}") {
  commentCount
    owner {
      nickname
      avatar
    }
    comments {
      commentUser {
        nickname
        avatar
      }
      content
      createdAt
      commentId
      images
    }
    createdAt
    content {
         desc
         img
    }
  }
}`
  const response = await fetch(`/graphql?query=${query}`);
  const { data } = await response.json();
  state.context.onSetMeta('og:type', 'article');
  state.context.onSetMeta('og:image', data.photo.cover);
  state.context.onSetMeta('og:title', data.photo.title);
  state.context.onSetMeta('og:description', data.photo.subTitle);
  state.context.onSetMeta('description', data.photo.subTitle);
  state.context.onSetTitle('个人作品');
  return <Photo photo= { data.photo } />;
};



