import React, { PropTypes } from 'react';
import withStyles from 'isomorphic-style-loader/lib/withStyles';
import s from './Strategy.scss';
import Comments from '../../components/Comment/Comments';
import Image from '../../components/Image/Image';
import Audio from '../../components/Audio/Audio';
import DateFormat from '../../util/dateFormat';
import NumberFormat from '../../util/NumberFotmat';
function renderTools(tools) {
  if (!tools) {
    return null;
  }
  return (
    <div className={ s.tools }>
      <div className= { s.stepHeader }>
        工具
      </div>
      <ul>
        {
          tools.map((value, key) => {
            return (
              <li key={ key }>
                <div>{ value.title }</div>
                <span>{ value.amount }</span>
              </li>
            );
          })
        }
      </ul>
    </div>
  );
}
function renderMaterials(materials) {
  if (!materials) {
    return null;
  }
  return (
    <div className={ s.materials }>
      <div className= { s.stepHeader }>
        用料
      </div>
      <ul>
        {
          materials.map((item, key) => {
            return (
              <li key={ key }>
                <div> { item.title } </div>
                <span> { item.amount } </span>
              </li>
            );
          })
        }
      </ul>
    </div>
  );
}

function renderSteps(steps) {
  if (!steps) {
    return null;
  }
  return (
    <div className={ s.steps }>
      <div className= { s.stepHeader }>
        步骤
      </div>
      <ul>
        {
          steps.map((item, index) => {
            return (
              <li key={ index }>
                <div className={ s.stepLeft }>
                  <span>
                    { index + 1 }
                  </span>
                </div>
                <div className={ s.stepRight }>
                  {item.imgUrl.length ? <Image src={ item.imgUrl } /> : ''}
                  <div dangerouslySetInnerHTML= {{ __html: item.description }} />
                </div>
              </li>
            );
          })
        }
      </ul>
    </div>
  );
}
function renderPhoto(photos) {
  if (photos.length === 0) {
    return null;
  }
  return (
    <div className= { s.photos }>
      <div className= { s.triangle }></div>
      <div className= { s.photo }>
        <p>
          <span>作品</span>
          <span>({ photos.length })</span>
        </p>
        <ul>
          {
            photos.map((item, index) => {
              if (index > 4) {
                return null;
              }
              return (
                <li key= { index } >
                  <Image height="40" width="40" src= { item.content[0].img }/>
                </li>
              );
            })
          }
        </ul>
      </div>
    </div>
  );
}

function renderTimeCircle(consumingTime) {
  if (!consumingTime) {
    return <noscript />;
  }
  let time = '';
  if (consumingTime === 1) {
    time = '< 30 分钟';
  } else if (consumingTime === 2) {
    time = '< 60 分钟';
  } else if (consumingTime === 3) {
    time = '< 90 分钟';
  }
  return (
    <li className="time">
            <span>
              用时
            </span>
      { time }
    </li>
  );
}
function renderScopeCircle(degree) {
  if (!degree || degree === 0) {
    return <noscript />;
  }
  const imgUrl = `../../toolbar_mark${degree}.png`;
  return (
    <li className="level">
            <span>
              难易度
            </span>
      <img src={ imgUrl } alt="" />
    </li>
  );
}
function renderFitCircle(scope) {
  if (!scope) {
    return <noscript />;
  }
  let text = '';
  if (scope === 1) {
    text = '1~2岁';
  } else if (scope === 2) {
    text = '3~4岁';
  } else if (scope === 3) {
    text = '5岁以上';
  }
  return (
    <li className="fit">
            <span>
              适用儿童
            </span>
      { text }
    </li>
  );
}
function renderChildFitWrap(description, consumingTime, degree, scope) {
  if (!description || description.length === 0) {
    return <noscript />;
  }
  return (
    <div className={s.childFitWrap}>
      <ul>
        { renderTimeCircle(consumingTime) }
        { renderScopeCircle(degree) }
        { renderFitCircle(scope) }
      </ul>
    </div>
  );
}
function renderAudio(url) {
  if (!url || url.length <= 0){
    return <noscript />;
  }
  return (
    <div className= { s.player }>
      <Audio src={ url } />
    </div>
  );
}
function renderEvent(events) {
  if (!events.length || events.length == 0) {
    return null;
  }
  return (
    <div className= { s.events }>
      <div className= { s.numbers }>
        <span>由妙招发起的活动&nbsp;&nbsp;</span>
        <span>({ events.length })</span>
      </div>
      <ul>
        {
          events.map((item, key) => {
            return (
              <li key= { key }>
                <Image height="132" width="228" src= { item.cover } />
                <div className= { s.body }>
                  <p className= { s.title }>{ item.title }</p>
                  <p className= { s.time }>{ DateFormat.commentDate(item.time) }</p>
                  <p className= { s.location }>{ item.location }</p>
                  <p className= { s.price }>{ item.price === '0' ? '免费' : `${NumberFormat.money(item.price)} 元` } </p>
                  <p className= { s.participate }>{ item.participantCount }<span>人参加</span></p>
                </div>
              </li>
            );
          })
        }
      </ul>
    </div>
  );
}
function Strategy({ strategy }) {
  return (
    <div className={s.container}>
      <div className={ s.cover }>
        <Image width="768" height="768" src={ strategy.cover } />
        <div className={ s.shadow } ></div>
      </div>
      <div className={ s.user }>
        <div className={ s.avatar }>
          <Image height="50" width="50" src={ strategy.owner.avatar } type="avatar" />
        </div>
        <div>
          <p className={ s.title }>{ strategy.title }</p>
          <p className={ s.nickName }>by<span>{ strategy.owner.nickname }</span></p>
        </div>
      </div>
      { renderChildFitWrap(strategy.description, strategy.consumingTime, strategy.degree, strategy.scope) }
      { renderAudio(strategy.soundStory) }
      <div className= { s.content } dangerouslySetInnerHTML= {{ __html: strategy.description ? strategy.description : '' }} >
      </div>
      <div className= { s.cook }>
        { renderTools(strategy.tools) }
        { renderMaterials(strategy.materials) }
        { renderSteps(strategy.description ? null : strategy.steps) }
      </div>
      <div className={ s.count }>
        <p className={ s.tryCount }>
          { strategy.tryCount }人参与
        </p>
        <p className={ s.collectCount }>
          { strategy.collectCount }人收藏
        </p>
      </div>
      { renderPhoto(strategy.photos) }
      { renderEvent(strategy.events) }
      <Comments comments={ strategy.comments } commentCount={ strategy.commentCount }/>
    </div>
  );
}
Strategy.propTypes = {
  strategy: PropTypes.object.isRequired,
};

export default withStyles(Strategy, s);



