import {
  GraphQLObjectType as ObjectType,
  GraphQLList as List,
} from 'graphql';

import PodcastType from './PodcastType';

const PodcastsType = new ObjectType({
  name: 'Podcasts',
  fields: {
    podcasts: { type: new List(PodcastType) },
  },
});

export default PodcastsType;



