import React, { PropTypes, Component } from 'react';
import withStyles from 'isomorphic-style-loader/lib/withStyles';
import s from './PodcastDetail.scss';
import ChatVoice from '../../components/ChatVoice/ChatVoice';
import ChatVideo from '../../components/ChatVideo/ChatVideo';
import _ from 'lodash';
var Realtime = require('leancloud-realtime').Realtime;
var realtime = new Realtime({
  appId: 'cbz3iy9f47mvirszhhfepphoyocm1uorzrps7w9tsb71h3ix',
  region: 'cn', // 美国节点为 "us"
});

class Podcast extends Component {

  constructor(props, context) {
    super(props, context);
    this.state = { messages: [], chatRoom: null, conversationId: null };
  }


  changeVoice(item) {
    this.setState({ audioUrl: item });
    this.refs.audio.pause();
    let self = this;
    setTimeout(function () {
      self.refs.audio.play();
    }, 150);
  }

  renderItem(item) {
    let message = item._lcattrs;
    return (
      <div key={message.id} className={s.message}>
        <img className={s.avatar}
          src={message.avatar.replace('http://static.qunzhibo.com/','http://static.qunkefu.com/')}
        />
        <div style={{ display: 'flex', flexDirection: 'column', marginLeft: 10, maxWidth:'90%' }}>
            <span className={s.name}>{message.nickname}</span>
            <span className={s.time}>{message.time}</span>
          {this.renderMessage(message)}
        </div>
      </div>
    );
  }

  renderMessage(item) {
    if(item.type == 'image') {
      return <div className={s.content} style={{ width: '80%' }}><img style={{ width: '100%' }} src={item.message.replace('http://static.qunzhibo.com/','http://static.qunkefu.com/')} alt=""/></div>;
    }else if(item.type == 'voice') {
      return (<ChatVoice audioUrl={item.message} isPlay={this.state.audioUrl == item.message} changeAudio={this.changeVoice.bind(this, item.message)}/>)
    }else if(item.type == 'link'){
      return <div className={s.content} style={{ width: '100%', display:'block', wordBreak: 'break-word' }}><a href={item.message}>{item.message}</a></div>;
    }else if(item.type == 'video') {
      return (<ChatVideo videoUrl={item.message.replace('http://static.qunzhibo.com/','http://static.qunkefu.com/')}/>);
    }
    else {
      return <div className={s.content}>{item.message}</div>;
    }

  }

  componentDidMount() {
    let self = this;
    realtime.createIMClient(this.props.userId).then(function(jerry) {
      self.setState({ chatRoom: jerry });
      jerry.on('message', function(message, conversation) {
        if(conversation.id == self.state.conversationId) {
          self.setState({ messages: self.state.messages.concat([message]) });
        }
      });
      var query = jerry.getQuery();
      return query
        .equalTo('name', self.props.podcastId)
        .find()
        .then(function(conversations) {
          if(conversations[0]) {
            return conversations[0];
          }
        })
        .catch(console.error.bind(console));
    }).then(function(conversation) {
      if(conversation) {
        self.setState({ conversationId: conversation.id });
        conversation.queryMessages({
          limit: 999, // limit 取值范围 1~1000，默认 20
        }).then(function(messages) {
          self.setState({ messages: messages });
        }).catch(console.error.bind(console));
      }
    });

    this.refs.audio.onended = function() {
      var messages = _.filter(self.state.messages, function(message) {
        return message._lcattrs.type == 'voice';
      });

      messages = _.map(messages, function(message) {
        return message._lcattrs.message;
      });

      var currentIndex = _.indexOf(messages, self.state.audioUrl);

      if(currentIndex + 1 < messages.length){
        self.changeVoice(messages[currentIndex + 1]);
      }
    };
  }

  componentWillUnmount() {
    this.state.chatRoom.close().then(function() {
    }).catch(console.error.bind(console));
  }



  createItems(items){
    var output = [];
    for(var i = 0; i < items.length; i++) output.push(this.renderItem(items[i]));
    return output;
  }

  render() {
    return <div className={s.container}>
      <audio className="player" ref="audio" src={this.state.audioUrl}>
      </audio>
      {this.state.messages.length>0?this.createItems(this.state.messages):<div className={s.loading}><div className={s.sprite}></div><p>加载中</p></div>}
    </div>
  }
}

Podcast.propTypes = {
};



export default  withStyles(Podcast, s);



