import React from 'react';
import PodcastDetail from './PodcastDetail';
import fetch from '../../core/fetch';
export const path = '/podcastdetail/*/view';
export const action = async (state) => {
  if(state.context.appData && state.context.appData.length>0) {
    let d = state.context.appData.replace(/&quot;/g,'"');
    let appData = JSON.parse(d);
    let token = appData.access_token;
    let userId = appData.userId;
    if(token) {
      const podcastId = state.path.replace('/podcastdetail/', '').replace('/view', '');
      const query = `{
  podcast(path: "${podcastId}", token: "${token}") {
    title
    joined
    roomNumber
  }
}
`;
      let response = await fetch(`/graphql?query=${query}`);
      let { data } = await response.json();
      state.context.onSetMeta('title', data.podcast.title);
      state.context.onSetMeta('og:title', data.podcast.title);
      return <PodcastDetail podcastId={data.podcast.roomNumber}  userId={ userId }/>;
      }

  }

  return <div></div>


};



