import {
  GraphQLObjectType as ObjectType,
  GraphQLList as List,
} from 'graphql';

import TopicType from './TopicType';

const TopicsType = new ObjectType({
  name: 'Topics',
  fields: {
    topics: { type: new List(TopicType) },
  },
});

export default TopicsType;



