import React from 'react';
import fetch from '../../core/fetch';
import Strategy from './Strategy';
export const path = '/strategies/*/view';
export const action = async (state) => {
  const strategyId = state.path.replace('/strategies/', '').replace('/view', '');
  const query = `{
  strategy(path: "${strategyId}") {
    owner {
      nickname
      avatar
    }
    soundStory
    title
    subTitle
    description
    cover
    tryCount
    score
    collectCount
    photoCount
    consumingTime
    commentCount
    degree
    scope
    photos {
      strategy
      photoId
      content {
        img
      }
    owner {
      nickname
      avatar
    }
      praiseCount
    }
    materials {
      amount
      title
    }
    steps {
      _id
      description
      imgUrl
      stepId
      timePoint
    }
    tools {
      amount
      title
    }
    comments {
      commentUser {
        nickname
        avatar
      }
      createdAt
      content
      commentId
      images
    }
    events {
      title
      location
      startDate
      price
      cover
      participantCount
    }
  }
}
`
  const response = await fetch(`/graphql?query=${query}`);
  const { data } = await response.json();
  const title = data.strategy.title;
  state.context.onSetMeta('og:type', 'article');
  state.context.onSetMeta('og:image', data.strategy.cover);
  state.context.onSetMeta('og:title', data.strategy.title);
  state.context.onSetMeta('og:description', data.strategy.subTitle);
  state.context.onSetMeta('description', data.strategy.subTitle);
  state.context.onSetMeta('format-detection', 'telephone-no');
  state.context.onSetTitle(title);
  return <Strategy strategy= { data.strategy } />;
};



